import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/dist/ScrollTrigger";

export default ()=>{
    let elements = document.querySelectorAll('.background__el'),
        main = document.querySelector('body');

    gsap.registerPlugin(ScrollTrigger);

    elements.forEach(el=>{
        let value = el.getAttribute('data-value');

        gsap.to(el, {
            scrollTrigger: {
                trigger: main,
                start: "top top",
                end: "bottom bottom",
                scrub: true,
                // markers: true,
            },
            y: -400 * (value * 1.2),
            ease: "none",
            duration: 2
        })
        
    });
}