import gsap from "gsap";

export default ()=>{
    let preloader = document.querySelector('.preloader'),
        composition = document.querySelector('.preloader__composition'),
        bg = document.querySelector('.preloader__bg'),
        hover = document.querySelector('.preloader__hover'),
        rotation = gsap.timeline();

    rotation.to(bg, {
        rotation: -360,
        ease: "none",
        duration: 5,
        repeat: -1
    });

    (function destroyPreloader() {
        let status = preloader.getAttribute('data-destroy');

        if(!status) {
            window.setTimeout(destroyPreloader, 100);
            return;
        }
        gsap.to(composition, {
            scale: 20,
            ease: "none",
            duration: 2,
            onComplete: ()=>{
                rotation.pause();
                setTimeout(()=>{
                    preloader.style.display = 'none';
                }, 1500);
            }
        });
        gsap.to(hover, {
            opacity: 1,
            ease: "none",
            duration: 1,
        })
        gsap.to(preloader, {
            opacity: 0,
            ease: "none",
            duration: 2,
            delay: 2,
        })

    })();

}