import capitalLetter from "./components/capitalLetter";
import customScroll from "./components/customScroll";
import themeSwitcher from "./components/themeSwitcher";




export default () => {

    if(localStorage.getItem('theme')) {
        let theme = localStorage.getItem('theme'),
            body = document.querySelector('body');
        
        if(theme == 'light') {
            body.classList.remove('dark-theme');
        } else if(theme == 'dark' && !body.classList.contains('dark-theme')) {
            body.classList.add('dark-theme'); 
        }
    }

    customScroll();

    if(document.querySelector('.theme-switcher')) {
        themeSwitcher();
    }

    if(document.querySelector('.capital-letter')) {
        capitalLetter();
    }

    

    if(document.querySelector('.preloader__bg')) {
        setTimeout(()=>{
            document.querySelector('.preloader').setAttribute('data-destroy', 'true');
        }, 1000)
    }
        
}
