import { gsap } from 'gsap';
import { ScrollTrigger } from "gsap/dist/ScrollTrigger";

export default ()=>{
    let triggers = document.querySelectorAll('.content__line');

    triggers.forEach(el=>{
        
        ScrollTrigger.create({
            trigger: el,
            start: "top 400px",
            end: "top 400px",
            // markers: true,
            onEnter: ()=>{
                let name = el.getAttribute('data-active');
                changeLink(name);
            },
            onEnterBack: ()=>{
                let name = el.getAttribute('data-active') - 1;
                changeLink(name);
            }
        });

    });

    function changeLink(name) {
        let cls = (window.innerWidth >= 840 ) ? 'links-block p' : 'content__title-mobile',
            links = document.querySelectorAll(`.${cls}`);

        links.forEach(el=>{
            el.classList.add('not-active');
        });

        document.querySelector(`.${cls}[data-ident="${name}"]`).classList.remove('not-active');
    }
}