export default ()=>{
    let letters = document.querySelectorAll('.capital-letter');

    letters.forEach(el=>{
        let text = el.textContent,
            letter = text.charAt(0),
            textMd = text.substring(1),
            span = document.createElement('span');

        span.classList.add('h1');
        span.textContent = letter;
        el.textContent = textMd;
        el.prepend(span);
    });
}