import gsap from "gsap";
import { ScrollTrigger } from "gsap/all";
import changeLink from "./changeLink";
import bgParralax from "./bgParralax";

export default ()=>{
    gsap.registerPlugin(ScrollTrigger);

    const scrollContainer = document.querySelector(".content");

    gsap.to(scrollContainer, {
        scrollTrigger: {
            trigger: document.querySelector('body'),
            start: "top top",
            end: "bottom bottom",
            // markers: true,
            scrub: true
        },
        y: -200,
        ease: "none",
        durattion: 6
    });


    if(document.querySelector('.background__el')) {
        bgParralax();
    }

    if(document.querySelector('.content__line')) {
        changeLink();
    }
}